.history {
    width: 100%;
    margin: 0 auto;
    padding: 3px;
    font-family: 'Courier New', Courier, monospace;
}

.history::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-image: linear-gradient(to bottom, rgba(213, 243, 210, 0.52), rgba(13, 86, 18, 0.73)), url("../images/dumbbell.png");
    opacity: .4;
    background-repeat: repeat;
    background-size: 50px;
    z-index: -1;
}

.history_header {
    position: sticky;
    height: 50px;
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0;
    padding: 1px;
    align-items: center;
    justify-content: center;
    border: rgb(13, 99, 39) solid 1px;
    background-color: #9e5f2f;
    border-radius: 15px;
    font-size: 16pt !important;
    font-weight: bold;
    color: black;
}

.history_content {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: auto;
    padding: 8px;
    align-items: flex-start;
    justify-content: center;
    max-height: calc(100vh - 180px);
    overflow-y: scroll;
}

.history_content_item {
    border: black solid 1px;
    border-radius: 5px;
    background-color: rgb(179, 138, 105, 0.8);
    width: 100%;
    padding: 10px;
    margin: 2px;
}

.history_item_header {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    margin-bottom: 5px;
    padding: 0;
    font-size: 15pt;
}

.history_item_list {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 0;
}

.history_item_name,
.history_item_date {
    font-weight: bold;
}

.history_exer_name {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 10pt;
}

.accordion {
    padding: 0;
}

.accordion-item {
    width: 100%;
    margin: 0;
    padding: 0;
}

.history_set_header, .history_set_content {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    font-size: 12pt;
}

.history_set_header {
    font-weight: bold;
}

.history_set, .history_value {
    align-content: center;
}

.history_set {
    text-decoration: underline;
}

.history_item_name {
    font-weight: bold;
}

.accordion-button:not(.collapsed), .accordion-button.collapsed {
    background-color: rgb(179, 119, 71);
}

.accordion-body {
    background-color: rgb(179, 165, 153);
}
.btn:focus,.btn:active {
    outline: none !important;
    box-shadow: none;
 }
