.newtemplate {
   width: 100%;
   margin: 0 auto;
   padding: 3px;
   font-family: 'Courier New', Courier, monospace;
}

.newtemplate::before {
   content: '';
   position: absolute;
   top: 0;
   right: 0;
   left: 0;
   bottom: 0;
   background-image: linear-gradient(to bottom, rgba(213, 243, 210, 0.52), rgba(13, 86, 18, 0.73)), url("../images/dumbbell.png");
   opacity: .4;
   background-repeat: repeat;
   background-size: 50px;
   z-index: -1;
}

.newtemplate_header {
   position: sticky;
   height: 50px;
   display: flex;
   flex-direction: row;
   width: 100%;
   margin: 0;
   padding: 0px;
   align-items: center;
   justify-content: space-between;
   border: rgb(13, 99, 39) solid 1px;
   background-color: #9e5f2f;
   border-radius: 15px;
   color: black;
}

.temp_header_text {
   font-weight: bold;
   font-size: 12pt;
}

.save_button,
.x_button {
   width: auto;
   height: 100%;
   background-color: transparent;
   outline: none;
   border: none;
   cursor: pointer;
}

.newtemplate_content {
   display: flex;
   flex-direction: row;
   width: 100%;
   margin: auto;
   padding: 0px;
   align-items: flex-start;
   justify-content: center;
   max-height: calc(100vh - 180px);
   overflow-y: scroll;
}

.newtemplate_content_form {
   display: flex;
   flex-direction: column;
   align-items: left;
   justify-content: flex-start;
   width: 100%;
   margin: 5px;
   padding: 0;
   border: black solid 1px;
   background-color: rgb(179, 119, 71, 0.8);
   border-radius: 10px;
}

.newtemplate_content_header {
   display: flex;
   flex-direction: row;
   align-items: left;
   justify-content: flex-start;
   margin-bottom: 5px;
   padding: 0;
   width: 100%;
}

.newtemplate_content_name {
   padding-top: 10px;
   padding-left: 20px;
}

.newtemplate_name_input {
   background-color: rgb(179, 165, 153);
   font-weight: bold;
}

.newtemplate_content_add {
   display: flex;
   flex-direction: row;
   align-items: left;
   justify-content: center;
   margin: 5px;
   padding: 0;
   width: 100%;
}

.newtemplate_content_add_button {
   display: flex;
   flex-direction: row;
   align-items: left;
   justify-content: center;
   margin: 5px;
   padding: 0;
   width: 100%;
}

.newtemplate_add_button,
.remove_button,
.reps_remove_button {
   width: auto;
   height: 100%;
   background-color: #9e5f2f;
   ;
   outline: none;
   border-radius: 10px;
   cursor: pointer;
   font-weight: bold;
}

.newtemplate_exer_list,
.newtemplate_exer_ele {
   display: flex;
   flex-direction: row;
   align-items: left;
   justify-content: center;
   margin: 0;
   padding: 0;
   width: 100%;
}

.newtemplate_exer_ele {
   border: black solid 1pt;
   border-radius: 5px;
   width: 98%;
   padding: 5px;
}

.newtemplate_exer_ele_header {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
   margin: 0;
   padding: 0;
   width: 100%;
}

.newtemplate_exer_ele_content {
   display: flex;
   flex-direction: row;
   align-items: left;
   justify-content: flex-start;
   margin: 0;
   padding: 0;
   width: 100%;
}

.newtemplate_exer_set_content,
.newtemplate_exer_set_header {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: flex-start;
   margin: 0;
   padding: 0;
   width: 100%;
}

.newtemplate_exer_pr, .newtemplate_exer_prev {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-around;
   margin: 0;
   padding: 2px;
   width: 100%;
}

.newtemplate_reps_add_set,
.newtemplate_rep_add_button {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
   margin: 0;
   padding: 2px;
   width: 100%;
}

.newtemplate_reps_button {
   display: flex;
   flex-direction: row;
   align-items: right;
   justify-content: flex-end;
   margin: 0;
   padding: 0;
   width: 100%;
}

#exerciseModal {
   margin-top: 100px;
}

.modal-content {
   background-color: rgb(179, 119, 71);
}

.select_exer {
   width: 98%;
   overflow: scroll;
   background-color: rgb(179, 165, 153);
}

.select_exer_title {
   margin-top: 10px;
}

.modal-header {
   background-color: #9e5f2f;
}
input {
   background-color: rgb(179, 165, 153);
   border: black solid 1pt;
}